@font-face {
  font-family: 'icon_font';
  src:
    url('./icon-fonts/icon_font.ttf?vwreeu') format('truetype'),
    url('./icon-fonts/icon_font.woff?vwreeu') format('woff'),
    url('./icon-fonts/icon_font.svg?vwreeu#icon_font') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class*="icon-"],
[class^="icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icon_font' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-Facebook .path1:before {
  content: "\e900";
  color: rgb(255, 255, 255);
}

.icon-Facebook .path2:before {
  content: "\e901";
  margin-left: -1em;
  color: rgb(20, 26, 32);
}

.icon-Google:before {
  content: "\e902";
  color: #fff;
}

.icon-Twitter:before {
  content: "\e903";
  color: #fff;
}